body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.dotsWrapper {
    bottom: 4%;
    position: absolute;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
}

.clickedDot, .inactiveDot {
    height: 10px;
    width: 10px;
    border: 2px solid white;
    border-radius: 50%;
    margin-right: 10px;
    cursor: pointer;
}

.clickedDot {
    background: white;
}
.slide {
    width: 100%;
    height: 100%;
    display: inline-block;
}
.leftArrow, .rightArrow {
    height: 50px;
    width: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    cursor: pointer;
    -webkit-transition: -webkit-transform ease-in .1s;
    transition: -webkit-transform ease-in .1s;
    transition: transform ease-in .1s;
    transition: transform ease-in .1s, -webkit-transform ease-in .1s;
}

.rightArrow {
    position: absolute;
    top: 50%;
    right: 25px;
    z-index: 999;
    color: #fff;
}

.leftArrow {
    position: absolute;
    top: 50%;
    left: 25px;
    z-index: 999;
    color: #fff;
}

html, body, .app, .main {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}

.slider {
    position: relative;
    width: 100%;
    margin: 0 auto;
    height: 100vh;
    overflow: hidden;
    white-space: nowrap;
}

.imagesWrapper {
    position: relative;
    height: 100%;
    width: 100%;
}
